@keyframes rotate {
  from {
    transform: rotate(0);
  }

  to {
    transform: rotate(360deg);
  }
}

* {
  box-sizing: border-box;
}

body {
  justify-content: center;
  align-items: center;
  height: 100vh;
  margin: 0;
  display: flex;
}

.stopwatch {
  border: 5px dotted #000;
  border-radius: 50%;
  width: 80vmin;
  height: 80vmin;
  position: relative;
}

.stopwatch__hang {
  transform-origin: bottom;
  animation-timing-function: linear;
  animation-iteration-count: infinite;
  position: absolute;
  left: 50%;
}

.stopwatch__hang--seconds {
  background-color: red;
  width: 3px;
  height: 50%;
  animation-name: rotate;
  animation-duration: 60s;
  left: calc(50% - 1.5px);
}

.stopwatch__hang--minutes {
  background-color: #00f;
  width: 5px;
  height: 25%;
  animation-name: rotate;
  animation-duration: 3600s;
  top: 25%;
  left: calc(50% - 2.5px);
}

.stopwatch__center {
  background-color: #000;
  border-radius: 50%;
  width: 10px;
  height: 10px;
  position: absolute;
  top: calc(50% - 5px);
  left: calc(50% - 5px);
}

